@import url('https://fonts.cdnfonts.com/css/melisa-2');
@import url('https://fonts.cdnfonts.com/css/super-vibes');
@import url('https://fonts.cdnfonts.com/css/overpass-mono-2');


@import url('https://fonts.cdnfonts.com/css/lovan');
@import url('https://fonts.cdnfonts.com/css/castlery');

body {
    align-items: center;
    justify-content: center;
    margin: 0;
    /* font-family: 'MELISA', sans-serif; */
    /* font-family: 'Finlandica', sans-serif; */
    font-family: 'Poppins', sans-serif;
    /* background-image: url('img/Illustration.jpg'); */
    /* background-image: url(https://i.pinimg.com/736x/fd/64/c0/fd64c0f24332664728e113c7ca223c27.jpg); */
    /* background-size: contain;  */
}

.melisa{
    font-family: 'MELISA', sans-serif;    
}

.supervibes{
    font-family: 'Super Vibes', sans-serif;
}

.overpass{
    font-family: 'Overpass Mono', sans-serif;
}


.link{
    text-decoration: none;
    display: inline; 
    cursor: pointer!important;                 
}

.right-text{
    text-align: right!important;
}


/* START NAV */

.navbar{
    background-color: transparent!important;
    font-family: 'MELISA', sans-serif!important; 
    box-shadow: 0 1px rgba(247, 247, 247, 0.215);
    /* filter: blur(8px);
    -webkit-filter: blur(8px); */
}

.navbar-bg{
    backdrop-filter: blur(4px);
    box-shadow: 0 1px 6px rgba(43, 43, 43, 0.107);
}

.nav-link {
    cursor: pointer!important;
}

.nav-link:hover{
    text-decoration: line-through;
}

.navbar-toggler{
    border: 0!important;
}


button:focus,.btn:active {
    outline: none !important;
    box-shadow: none!important;
}

/* END NAV */

 
/*  START FOOTER */

.footer{
    position: fixed;
    bottom: 0;
    background-color: transparent !important;
    z-index: 1;
}

.footer-icon:hover{
    color: #7CDE76!important;
    /* transition: transform 0.3s ease!important;  */
}

.footer-email{
    writing-mode: vertical-rl; 
  
}

@media only screen and (max-width: 1024px) {
   .footer{
    display: none !important;
   }
}





