/* START ABOUT */

.about {
    /* height: 750px; */
    background-color: #FAEEDE;
    margin-top: -33px;  
    background-image: url(img/Illustration2.png);
    /* background-position: center;
    background-size: contain;
    background-repeat: no-repeat; */
}

.about-frame{
    position: relative;
    width: 95%;
    height: 460px;
    background-color: rgb(33, 34, 40);
    border-radius: 20px;
    box-shadow: 8px 7px rgb(234, 188, 102);
}

.about-bg{
    background-image: url(img/three.jpg);
    background-size: cover;
    height: 80%;
}

#hr1{
    width: 20%;
    border-top: 7px solid #f1c169;
    opacity: 1;
}

.about-title{
    position: relative;
    z-index: 1;
}

.img-1{
    position: absolute;
    left: -4%;
    top: -15px;
    transform: skew(10deg);
    transform: rotate(-10deg); 
    /* z-index: 1;  */
}

.img-2{
    /* position: absolute;
    top: 1150px; */
    transform: skew(10deg);
    transform: rotate(-15deg); 
    z-index: 1; 
}

.img-3{
    /* position: absolute;
    left: 18%;
    top: 1150px; */
    transform: skew(10deg);
    transform: rotate(15deg); 
    z-index: 1; 
}

.img-4{
    /* position: absolute;
    left: 28%;
    top: 1150px; */
    transform: skew(10deg);
    transform: rotate(29deg); 
    z-index: 1; 
}

.img-5{
    /* position: absolute;
    left: 35%;
    top: 1150px; */
    transform: skew(10deg);
    transform: rotate(5deg); 
    z-index: 1; 
}

.about img:hover{
    transform: scale(1.2); 
    transition: transform .4s ease;
}

.bounce { 
    animation-name: bounce; 
} 

span.letter:hover{
    color:  #f1c169!important;
    animation: bounce; 
    animation-duration: 2s; 
}

.about-skills{
    background-color:#f1c169;
    padding: 8px;
    border-radius: 14px;
    border: 1px solid rgba(0, 0, 0, 0.415);
}

.about-text{
    border: 1px solid black;
    padding: 10px;
    border-radius: 50px;
}



/* START WORKS */


.works {
    /* height: 750px; */
    background-color: #FAEEDE;
    background-image: url('img/Illustrations.jpg');
    background-size: cover; 
    background-repeat: no-repeat;
    z-index: 1;
}

.work-btn{
    background-color: transparent;
    color:  #FAEEDE;
    border: none;
    font-weight: 800;
}

.work-btn:hover{
